import routers from './routers';
import { makeAccent } from '../events/clickListener';
import preload from '../utils/preload';

import {
  measureAndFixScroll,
  addPreloader,
  delPreloader,
} from '../utils/preloader';

let q = '';
if (location.href.includes('github')) {
  q = 'NstdServers';
}

let auth = true;
let startState = true;

window.onload = () => {
  addPreloader(document.body, true);
  decideRout(location.pathname, true);
};

window.onpopstate = () => {
  const state = decideRout(location.pathname, true);
};

function decideRout(path, replace = false) {
  if (path.includes('Server')) {
    let newPath =
      path
        .split('/')
        .filter((p, i, arr) => p.toLowerCase() !== 'nstdservers')
        .join('/') + '/';
    path = newPath;
  }
  let rout = routers.find(el => el.path === path);
  if (rout && !startState)
    replace
      ? history.replaceState({ page: rout.page }, rout.title, path)
      : history.pushState({ page: rout.page }, rout.title, path);
  rout.component();
  preload(rout);
  makeAccent(rout);
  startState = false;
  return rout;
}

export { decideRout };
